  .green {
    background-color: #209B47;
  }
  .offwhite {
    background-color: #FAF9F6;
  }
  .top_triangle_divider {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.top_triangle_divider svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 25px;
}

.top_triangle_divider .shape-fill {
    fill: #FAF9F6;
}
.spacer {
  aspect-ratio: 960/300;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.layer1 {
  background-image: url(layered-waves.svg);
}
